"use client"

import Link from "next/link"
import { useState } from "react"
import { Menu, X, Facebook, Twitter, Instagram, Linkedin, Youtube } from "lucide-react"
import Image from "next/image"

export function Navigation() {
  const [isOpen, setIsOpen] = useState(false)

  const links = [
    { href: "/", label: "Home" },
    { href: "/about", label: "About" },
    { href: "/our-work", label: "Our Work" },
    { href: "/team", label: "Team" },
    { href: "/contact", label: "Contact Us" },
  ]

  const socialLinks = [
    { 
      href: "https://facebook.com", 
      label: "Facebook", 
      icon: <Facebook className="w-4 h-4" />,
      ariaLabel: "Visit our Facebook page"
    },
    { 
      href: "https://twitter.com", 
      label: "Twitter", 
      icon: <Twitter className="w-4 h-4" />,
      ariaLabel: "Visit our Twitter page"
    },
    { 
      href: "https://instagram.com", 
      label: "Instagram", 
      icon: <Instagram className="w-4 h-4" />,
      ariaLabel: "Visit our Instagram page"
    },
    { 
      href: "https://linkedin.com", 
      label: "LinkedIn", 
      icon: <Linkedin className="w-4 h-4" />,
      ariaLabel: "Visit our LinkedIn page"
    },
    { 
      href: "https://youtube.com", 
      label: "YouTube", 
      icon: <Youtube className="w-4 h-4" />,
      ariaLabel: "Visit our YouTube channel"
    },
  ]

  return (
    <nav className="sticky top-0 z-50 w-full bg-background border-b border-border">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-20">
          {/* Logo and Foundation Name */}
          <Link href="/" className="flex items-center gap-2 font-bold text-xl">
            <div className="w-8 h-8 bg-foreground rounded-full flex items-center justify-center">
              <Image src={"/images/asf-logo.png"} alt="logo" height={50} width={50}/>
            </div>
            <span className="hidden sm:inline">Aapka Sahyog Foundation</span>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden lg:flex gap-8 items-center">
            {links.map((link) => (
              <Link
                key={link.href}
                href={link.href}
                className="text-sm font-medium hover:text-muted-foreground transition-colors duration-200"
              >
                {link.label}
              </Link>
            ))}
          </div>

          {/* Desktop Social Media and Donate Button */}
          <div className="hidden lg:flex items-center gap-4">
            {/* Social Media Icons */}
            <div className="flex items-center gap-2 mr-2">
              {socialLinks.map((social) => (
                <a
                  key={social.href}
                  href={social.href}
                  target="_blank"
                  rel="noopener noreferrer"
                  aria-label={social.ariaLabel}
                  className="p-2 text-muted-foreground hover:text-foreground hover:bg-secondary rounded-full transition-colors duration-200"
                >
                  {social.icon}
                </a>
              ))}
            </div>
            
            {/* Donate Button */}
            <Link
              href="/get-involved"
              className="px-6 py-2 bg-foreground text-background rounded-lg font-semibold hover:bg-muted-foreground transition-colors duration-200 whitespace-nowrap"
            >
              Donate Now
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={() => setIsOpen(!isOpen)}
            className="lg:hidden p-2 hover:bg-secondary rounded-lg transition-colors"
            aria-label={isOpen ? "Close menu" : "Open menu"}
          >
            {isOpen ? <X size={20} /> : <Menu size={20} />}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isOpen && (
          <div className="lg:hidden pb-4 border-t border-border">
            {/* Navigation Links */}
            <div className="space-y-2 py-4">
              {links.map((link) => (
                <Link
                  key={link.href}
                  href={link.href}
                  className="block px-4 py-2 text-sm font-medium hover:bg-secondary rounded-lg transition-colors"
                  onClick={() => setIsOpen(false)}
                >
                  {link.label}
                </Link>
              ))}
            </div>

            {/* Mobile Social Media Links */}
            <div className="px-4 py-4 border-t border-border">
              <p className="text-sm font-medium text-muted-foreground mb-3">Follow Us</p>
              <div className="flex items-center gap-3">
                {socialLinks.map((social) => (
                  <a
                    key={social.href}
                    href={social.href}
                    target="_blank"
                    rel="noopener noreferrer"
                    aria-label={social.ariaLabel}
                    className="p-2 text-muted-foreground hover:text-foreground hover:bg-secondary rounded-full transition-colors duration-200"
                    onClick={() => setIsOpen(false)}
                  >
                    {social.icon}
                  </a>
                ))}
              </div>
            </div>

            {/* Mobile Donate Button */}
            <div className="px-4 pt-2">
              <Link
                href="/get-involved"
                className="block w-full px-4 py-3 bg-foreground text-background rounded-lg font-semibold text-center hover:bg-muted-foreground transition-colors"
                onClick={() => setIsOpen(false)}
              >
                Donate Now
              </Link>
            </div>
          </div>
        )}
      </div>
    </nav>
  )
}